import React from 'react';
import Frame from '../components/frame/frame';

import api from '../lib/api';
import Headline from '../components/headline/headline';
import HowWeWork from '../components/howwework/howwework';
import Subline from '../components/subline/subline';
import ContactAction from '../components/contactaction/contactaction';

type Props = {
  howWeWork: Array<{ title: string; description: string }>;
  text: string;
  pageTitle: string;
  pageDescription: string;
};

export default function Arbeitsweise(data: Props) {
  return (
    <Frame singlePage title={data.pageTitle} description={data.pageDescription}>
      <Headline level={1}>{data.pageTitle}</Headline>
      <Subline small>{data.pageDescription}</Subline>
      <HowWeWork data={data.howWeWork} />
      <ContactAction />
    </Frame>
  );
}

Arbeitsweise.getInitialProps = () => api('Arbeitsweise');
